body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, Arial, sans-serif;
}

:root {
  --active-color: #00acdc;
}

.header {
  align-items: center;
  width: 100%;
  display: flex;
}

.header_logo {
  margin: 10px 0 0 50px;
}

.header_nav {
  flex-grow: 1;
  justify-content: flex-end;
  margin-right: 50px;
  display: flex;
}

.header_nav_list {
  margin: 0;
  list-style: none;
  display: flex;
}

.header_nav_link:not(:first-child) {
  margin-left: 20px;
}

.link {
  text-transform: uppercase;
  color: #000;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  position: relative;
}

.link:hover {
  color: var(--active-color);
}

.is-active:after {
  content: "";
  background-color: var(--active-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: -18.5px;
  left: 0;
}
